@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  height: 100%;
  box-sizing: border-box; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff; }

main {
  flex-grow: 1; }

header {
  flex-grow: 0; }

footer {
  flex-grow: 0; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.hidden {
  display: none !important; }

.container {
  margin: 0 auto;
  width: 1320px;
  max-width: 100%;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: center; }

.container-big {
  margin: 0 auto;
  width: 1920px;
  max-width: 100%; }

@media (max-width: 1100px) {
  .container {
    padding: 0 15px; } }

.main {
  display: flex;
  align-items: center;
  justify-content: center; }
  .main a {
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 10vw;
    font-weight: 500;
    line-height: 1;
    letter-spacing: 0;
    color: #11A0F8;
    text-align: center; }
    .main a:hover, .main a:focus, .main a:active {
      cursor: pointer;
      text-decoration: underline;
      opacity: 0.6; }

.popup {
  position: fixed;
  z-index: 50;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(61, 70, 77, 0.7); }

.popup__close {
  outline: none;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none; }
  .popup__close svg {
    fill: #90A4AE; }
  .popup__close:hover, .popup__close:focus, .popup__close:active {
    cursor: pointer; }
    .popup__close:hover svg, .popup__close:focus svg, .popup__close:active svg {
      fill: #000000; }

.popup__block {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  max-width: 100%;
  background-color: #ffffff;
  border-radius: 2px;
  overflow: hidden; }

.popup__head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 77px;
  padding: 5px 20px;
  border-bottom: 1px solid #DAE3EA;
  background: rgba(218, 227, 234, 0.3); }

.popup__caption {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0;
  color: #3D464D;
  text-transform: uppercase; }

.popup__main {
  display: flex;
  padding: 19px 0 20px 0;
  border-bottom: 1px solid #DAE3EA; }

.popup__img {
  flex: 0 0 auto;
  padding: 0 24px; }

.popup__column {
  flex: 0 1 auto;
  padding-right: 20px; }

.popup__speach {
  max-width: 100%;
  margin-bottom: 10px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
  letter-spacing: 0;
  color: #2F363C; }

.popup__text {
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
  letter-spacing: 0;
  color: #7E939E; }

.popup__footer {
  padding: 5px 20px;
  min-height: 71px;
  display: flex;
  align-items: center;
  justify-content: flex-end; }

.popup__footer-btn {
  outline: none;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  min-height: 30px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0;
  color: #11A0F8;
  border: 1px solid #11A0F8;
  border-radius: 2px; }
  .popup__footer-btn:hover, .popup__footer-btn:focus, .popup__footer-btn:active {
    cursor: pointer;
    text-decoration: underline;
    opacity: 0.6; }
  .popup__footer-btn:first-child {
    border: none; }

@media (min-width: 768px) {
  .popup__block {
    width: 615px; }
  .popup__caption {
    font-size: 18px; }
  .popup__speach,
  .popup__text {
    font-size: 16px; }
  .popup__img svg {
    width: 30px;
    height: 30px; }
  .popup__footer-btn {
    width: 60px;
    min-height: 40px;
    font-size: 16px; } }
